.control {
  padding: 0 10px;
  display: flex;
  flex-direction: column;

  &__item {
    flex-shrink: 0;

    &._btn {
      flex-shrink: 1;
      position: fixed;
      bottom: 0;
      left: 0;
      margin-top: 15px;
      height: 65px;
      width: 100%;
      z-index: 10;
    }
  }

  @media #{'(min-width: 768px)'} {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    .control {
      padding: 0;

      &__item {
        margin-right: 15px !important;

        &._btn {
          position: static;
          margin-top: 0;
          height: 40px;
          width: 300px;
        }
      }
    }
  }
}
