.totop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 120px;
  display: none;
  justify-content: center;
  align-items: flex-end;
  z-index: 1101;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.2s;
  color: white;

  &:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }

  @media #{'(min-width: 768px)'} {
    display: flex;
  }
}
