.card {
  position: relative;
  align-self: stretch;
  margin-right: -15px;
  margin-left: -15px;
  margin-bottom: 25px;
  width: 100%;
  background-color: rgba(156, 157, 162, 0.24);
  box-shadow: 5px 5px 12px 0 rgba(50, 50, 50, 0.45);
  transition: all 0.5s;

  &:last-child,
  &:last-of-type {
    margin: 0;
  }

  &:hover {
    transform: scale(1.02);
  }

  &__container {
    display: flex;
    justify-content: space-around;
    position: relative;
    top: -4px;
    margin-bottom: 8px;
    padding: 5px 30px 5px 10px;
    background-color: rgba(156, 157, 162, 0.27);
  }

  &__link {
    text-decoration: none;
  }

  &__img {
    width: 100%;
    height: 200px;
  }

  &__title {
    padding: 5px 30px 5px 10px;
    font-family: 'Roboto';
    margin: 0;
    color: rgba(156, 157, 162, 1);
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__order {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 0;
    padding: 7px;
    background-color: #3f51b5;
    color: white;
  }

  &__text {
    margin: 0;
    color: rgb(151, 152, 157);
  }

  @media #{'(min-width: 768px)'} {
    margin-right: 15px;
    margin-left: 0;
    margin-bottom: 15px;
    width: 370px;
    border-radius: 6px;

    .card {
      &__img {
        border-radius: 6px 6px 0 0;
      }

      &__order {
        border-radius: 0 0 6px 0;
      }

      &__container {
        border-radius: 0 0 6px 6px;
      }
    }
  }
}
